import React from 'react';
import moment from 'moment';
import { Line } from 'react-chartjs-2';
import { Col, Row, Typography } from 'antd';

const { Title } = Typography;

const LineChart = ({ coinHistory, currentPrice, coinName }) => {
  const coinPrice = [];
  const coinTimestamp = [];

  for (let i = 0; i < coinHistory?.data?.history?.length; i += 1) {
    coinPrice.push(coinHistory?.data?.history[i].price);
    // coinTimestamp.push(
    //   new Date(coinHistory?.data?.history[i].timestamp).toLocaleDateString()
    // );
    coinTimestamp.push(
      new Date(
        coinHistory?.data?.history[i].timestamp * 1000
      ).toLocaleDateString()
    );
  }

  // console.log(coinTimestamp);

  console.log(coinHistory);

  // for (let i = 0; i < coinHistory?.data?.history?.length; i += 1) {
  //   // let newDate = moment
  //   //   .unix(coinHistory?.data?.history[i].timestamp)
  //   //   .format('DD/MM/YYYY');
  //   // coinTimestamp.push(newDate);

  //   let timestamp = coinHistory?.data?.history[i].timestamp;
  //   // let formatted = moment(timestamp).format('L');

  //   // coinTimestamp.push(formatted);

  //   let formatted = new Date(timestamp * 1000).toLocaleDateString();

  //   coinTimestamp.push(formatted);

  //   // coinTimestamp.push(
  //   //   new Date(coinHistory?.data?.history[i].timestamp).toLocaleDateString()
  //   // );
  // }
  const data = {
    labels: coinTimestamp,
    datasets: [
      {
        label: 'Price In USD',
        data: coinPrice,
        fill: false,
        backgroundColor: '#0071bd',
        borderColor: '#0071bd',
      },
    ],
  };

  const options = {
    scales: {
      yAxes: [
        {
          ticks: {
            beginAtZero: true,
          },
        },
      ],
    },
  };

  // const options = {
  //   scales: {
  //     yAxes: [
  //       {
  //         display: true,
  //         ticks: {
  //           suggestedMin: 0, // minimum will be 0, unless there is a lower value.
  //           // OR //
  //           beginAtZero: true, // minimum value will be 0.
  //         },
  //       },
  //     ],
  //   },
  // };

  return (
    <>
      <Row className='chart-header'>
        <Title level={2} className='chart-title'>
          {coinName} Price Chart{' '}
        </Title>
        <Col className='price-container'>
          <Title level={5} className='price-change'>
            Change: {coinHistory?.data?.change}%
          </Title>
          <Title level={5} className='current-price'>
            Current {coinName} Price: $ {currentPrice}
          </Title>
        </Col>
      </Row>
      <Line data={data} options={options} />
    </>
  );
};

export default LineChart;
